<template>
  <div>
    <Headerbox></Headerbox>
    <el-main class="main">
    <el-page-header @back="goBack" content="兴趣点"></el-page-header>
    <div class="itemsName">
      <img src="../../public/images/项目.png" />
      <span>我的项目({{itemsNum}})</span>
    </div>

    <el-button
      class="addItems"
      type="primary"
      icon="el-icon-plus"
      @click="dialogTableVisible = true"
    >创建项目</el-button>
    <!-- 添加弹框 -->
    <!-- @close="addDialogClose" // 关闭弹框的回调(用于清空表单)
    :visible.sync="dialogTableVisible" // 弹框是否开启
    :close-on-click-modal="false" // 是否可以通过点击任意位置关闭Dialog弹框-->
    <el-dialog
      title="创建项目"
      @close="addDialogClose"
      :visible.sync="dialogTableVisible"
      :close-on-click-modal="false"
    >
      <!-- 添加用户弹框 -->
      <el-form
        ref="addFormRef"
        :rules="rulesCreProj"
        :model="creProj"
        label-width="100px"
        size="mini"
      >
        <el-form-item class="big" prop="projname" label="项目名称：">
          <el-input v-model="creProj.projname"></el-input>
        </el-form-item>
        <!-- @change="cityChange" -->
       

      <!-- <el-form-item class="big" prop="spidercity" label="上传抓取城市的geojson">
        <el-row>
            <el-col :span="12">
<el-upload
  class="upload-demo"
  ref="upload"
  action="https://jsonplaceholder.typicode.com/posts/"
  :on-preview="handlePreview"
  :on-remove="handleRemove"
  :before-upload="beforeupload"
  :file-list="fileList"
  :auto-upload="true">
  <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
  <!- <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button> -->
  <!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
<!-- </el-upload> -->
            <!-- </el-col> -->
<!-- </el-row> -->
       
  <!-- </el-form-item> --> 
 






        <el-form-item class="big" prop="spidercity" label="抓取城市：">
          <el-row>
            <el-col :span="12">
              <div class="cityLevel">省市区</div>
              <el-select
                v-model="creProj.provinceObj"
                value-key="code"
                placeholder="请选择"
                @change="search"
              >
                <el-option
                  v-for="item in provinces"
                  :key="item.code"
                  :label="item.name"
                  :value="item"
                ></el-option>
              </el-select>
            </el-col>
            <el-col :span="12">
              <div class="cityLevel">地级市</div>
              <el-select
                v-model="creProj.cityObj"
                value-key="code"
                placeholder="请选择"
                @change="cityChanged"
              >
                <el-option
                  v-for="item in cities"
                  :key="item.code"
                  :label="item.name"
                  :value="item"
                ></el-option>
              </el-select>
            </el-col>
          </el-row>
        </el-form-item>

        <!-- <el-form-item class="big" prop="projname" label="引用资源：">
          <el-input v-model="creProj.scriptResource"></el-input>
        </el-form-item>

      <el-form-item class="big" prop="projname" label="抓取逻辑：">
          <el-input type="textarea" :rows="6"
          v-model="creProj.searchFunc"></el-input>
        </el-form-item> -->

        <el-row>
          <el-col :span="12">
            <el-form-item class="small" prop="spidertype" label="抓取类型：">
              <el-select v-model="creProj.spidertype" placeholder="请选择">
                <el-option
                  v-for="item in spiderTypes"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item>
          <el-button @click="dialogTableVisible = false">取消</el-button>
          <el-button type="primary" @click="onCreProj">确定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!-- 初始状态没有项目时显示,noProj返回true时显示 -->
    <el-col :span="24" v-if="noProj">
      <el-card shadow="never">
        您还没有创建项目，现在去
        <span @click="dialogTableVisible = true" class="creatspan">创建一个项目 </span>吧！
      </el-card>
    </el-col>

    <!-- 已有项目的操作框，当初始状态有项目时显示 -->
    <ul id="projLists" v-else>
      <li v-for="(item,index) in ExistProjDatas" v-bind:key="index">
        <el-col :span="24">
          <el-card class="projBox" shadow="hover">
            <div slot="header" class="boxName">
              <div style="margin:-18px -20px; padding:10px 20px;height:30px">
                <span>{{item.name}}</span>
                <el-button
                  v-bind="{item:item}"
                  style="float: right; margin:0; padding: 0 0 0 5px;line-height:30px"
                  type="text"
                  @click="delectProject(item)"
                ><img src="../assets/images/delete.png"></el-button>
                <el-button
                  v-bind="{item:item}"
                  style="float: right; margin:0; padding: 0 5px 0 5px;line-height:30px"
                  type="text"
                  @click="exportCSV(item)"
                ><img src="../assets/images/csv.png"></el-button>
                <el-button
                  v-bind="{item:item}"
                  style="float: right; margin:0; padding: 0 5px 0 0;line-height:30px"
                  type="text"
                  @click="viewDatas(item)"
                ><img src="../assets/images/seemap.png"></el-button>
              </div>
            </div>
            <div class="boxContent">
              <div class="leftcon">
                <div>
                  <span class="hn">创建时间：</span>
                  <span class="cn">{{item.createtime.toLocaleString()}}</span>
                </div>
                <div>
                  <span class="hn">爬取城市：</span>
                  <span class="cn">{{item.province}}-{{item.cityName}}</span>
                </div>
                <div>
                  <span class="hn">爬取类型：</span>
                  <span class="cn">{{item.keyWords}}</span>
                </div>
              </div>
              <div class="rightcon">
                <!-- <el-progress type="circle" :percentage="(item.progress*100).toFixed(2)" :width="90"></el-progress> -->
                <div>
                  <span class="hn">爬取状态：</span>
                  <span class="cn" v-if="item.progress !==1">拼命爬取中</span>
                  <span class="cn" v-else>已完成</span>
                </div>
                <div>
                  <span class="hn">完成情况：</span>
                  <span class="cn" v-if="item.progress !==1">{{(item.progress*100).toFixed(6)}}%</span>
                  <span class="cn" v-else>{{item.progress*100}}%</span>
                </div>
              </div>
            </div>
          </el-card>
          <progressbar v-if="item.progress !==1" :height="5" :percentage="item.progress*100"></progressbar>
        </el-col>
      </li>
    </ul>
    </el-main>
  </div>
</template>

<script>
import Dexie from 'dexie';
import pcs from "../fetchers/pcs.js";
import Crawler from '../fetchers/crawler.js'
import formatTime from '../utils/timeformatter';
let lodash = require('lodash');
export default {
  name: "Xingqudian",
  _crawler:null,
  data() {
    return {
      //itemsNum: 0,
      dialogTableVisible: false,
      //创建项目
      creProj: {
        projname: "",
        spidertype: "",
        scriptResource:"https://webapi.amap.com/maps?v=1.4.15&key=0971acd2037ad6fda6f8020298651877&plugin=AMap.PlaceSearch",
        searchFunc:'function search(bounds, keywords, cityname, pageindex, pagesize){let option = {city: cityname, type: keywords,pageSize: pagesize,pageIndex: pageindex,};let searcher = new AMap.PlaceSearch(option);let bs = new AMap.Bounds(new AMap.LngLat(bounds[0], bounds[1]), new AMap.LngLat(bounds[2], bounds[3]));searcher.searchInBounds(keywords, bs,(str, res) => {if (str === "no_data") {this.continueWorking(false, this.parseIsLastIdx(res), null);} else if (str === "error") {this.continueWorking(true);} else {let pois = this.parsePOIArr(res);let isLastIdx = this.parseIsLastIdx(res);this.continueWorking(false, isLastIdx, pois);}});}',
        provinceObj: {},
        cityObj: {},
        cityGeoJson:{}
      },
      spiderTypes: [],
      districtSearch: undefined,
      provinces: [],
      cities: [],

      // 验证规则
      rulesCreProj: {
        projname: [
          { required: true, message: "请输入项目名", trigger: "blur" }
        ],
        spidertype: [
          { required: true, message: "请输入抓取类型", trigger: "blur" }
        ],
        
      },

      //已有项目的数据，为一个数组对象
      ExistProjDatas: []
      //已有项目的进度
    };
  },
  computed: {
    noProj: function() {
      if (this.ExistProjDatas.length === 0) {
        return true;
      } else {
        return false;
      }
    },
    itemsNum: function() {
      return this.ExistProjDatas.length;
    }
  },
 watch:{
    ExistProjDatas:function(){
      //this.initExistProjDatas();
      this.debouncedExistProjDatas();
    }
  },
  created(){
    this.initExistProjDatas();
    this.debouncedExistProjDatas = _.debounce(this.initExistProjDatas,2000);
  },
  mounted() {
    this.initCity();
    this.initType();
  },
 
  methods: {
    //返回上一级
    goBack() {
      this.$router.push({
        name: "catalog"
      });
    },
    //初始化已有项目
    async initExistProjDatas() {
      
        let _crawler = new Crawler.Crawler();
        await _crawler.init();
      _crawler.getManyPoiTaskInfo(1,10).then(x => {
        this.ExistProjDatas = x.data;
      });
      
      // this.$axios.get("/poitask/PoiTask?pageidx=1&pagesize=10").then(res => {
      //   this.ExistProjDatas = res.data.data;
      // });
    },
    // 关闭弹框的回调
    addDialogClose() {
      this.$refs.addFormRef.resetFields(); // 清空表单
    },
    //初始化弹框中的省份选择
    initCity() {
      console.log(pcs);
      console.log(pcs.length);
        this.provinces = [];
        this.cities = [];
        pcs.pcs.forEach(x => {
           this.provinces.push(x);
        });
      // AMap.plugin("AMap.DistrictSearch", function() {
      //   let districtSearch = new AMap.DistrictSearch({
      //     level: "country",
      //     subdistrict: 1
      //   });
      //   districtSearch.search("中国", function(status, result) {
      //     if (status === "complete") {
      //       let data = result.districtList[0];
      //       let level = undefined;
      //       that.getData(data, level);
      //     } else {
      //       console.log("err");
      //     }
      //   });
      //   that.districtSearch = districtSearch;
      // });
    },
    getData(data, level) {
      if (level === "province") {
        this.cities = [];
      }
      let subList = data.districtList;
      if (subList) {
        let culLevel = subList[0].level;
        if (culLevel === "province") {
          this.traverseSubList(subList, culLevel);
        } else if (culLevel === "city") {
          this.traverseSubList(subList, culLevel);
        }
      }
    },
    traverseSubList(subList, culLevel) {
      subList.forEach(e => {
        let obj = {};
        obj.adcode = e.adcode;
        obj.name = e.name;
        obj.level = e.level;
        if (culLevel === "province") {
          this.provinces.push(obj);
        } else if (culLevel === "city") {
          this.cities.push(obj);
        }
      });
    },
    cityChanged(obj){
      let cityCode = this.creProj.cityObj.code;
      let code = `${cityCode}`;
      let url = `geojsons/${code}.json`;
      
      this.$axios.get(url).then(x => {
        this.creProj.cityGeoJson = x.data;
        console.log(x.data);
      });
      
    },
    search(obj) {
      let cities = this.creProj.provinceObj.children;
      this.cities = [];
      for(let i = 0;i < cities.length;i++){
        this.cities.push(cities[i]);
      }

      // this.districtSearch.setLevel(level);
      // this.districtSearch.setExtensions("all");
      // this.districtSearch.search(adcode, function(status, result) {
      //   if (status === "complete") {
      //     that.getData(result.districtList[0], level);
      //   }
      // });
    },
    //初始化选择的类型
    initType() {
      let that = this;
      that.spiderTypes = [];
      let dataArr = Crawler.Crawler.GetPoiTypes();
      for(let i = 0;i < dataArr.length;i++){
        let obj = {
          label: dataArr[i],
          value: dataArr[i]
        }
        that.spiderTypes.push(obj);
      }
      // this.$axios
      //   .get("/Poi/types")
      //   .then(function(response) {
      //     let dataArr = response.data.data;
      //     for (let i in dataArr) {
      //       let obj = {
      //         label: dataArr[i],
      //         value: dataArr[i]
      //       };
      //       that.spiderTypes.push(obj);
      //     }
      //   })
      //   .catch(function(error) {
      //     console.log(error);
      //   });
    },
    // //查询某个POI任务进度
    // getProstate(id,res) {
    //   this.$axios.get("/poitask/PoiTask/Progress/" + id).then(res => {

    //     let progress = (res.data.data * 100).toFixed(2);
    //     res.prostate = progress;
    //     console.log(this.ExistProjDatas);
    //   });
    // },
    
    //创建一个新的POI抓取任务
    async createProject(name, cityGeoJson, keyWords,resources,searchFUncStr) {
      let _crawler = new Crawler.Crawler();
      await _crawler.init();
      let createParas = {
        name: name,
        geojson: cityGeoJson,
        width: 400,
        keywords: keyWords,
        resources:resources,
        searchFncStr:searchFUncStr
      };
      await _crawler.crateCrawlTask(createParas);
      this.initExistProjDatas();
      this.$message.success("创建成功");
      // this.$axios
      //   .post("/poitask/PoiTask", {
      //     name: name,
      //     cityAdCode: cityAdCode,
      //     pageSize: 0,
      //     width: 400,
      //     keyWords: keyWords
      //   })
      //   .then(res => {
      //     let id = res.data.data;
      //     this.initExistProjDatas();
      //     this.$message.success("创建成功");
      //     let that = this;
      //     // setInterval(function() {
      //     //   //that.getProstate(id,res);
      //     //   that.initExistProjDatas();
      //     // }, 4000);
      //   });
    },
    //弹出框确定事件
    onCreProj() {
      let that = this;
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return null; // 如果验证失败就不往下继续执行

        that.dialogTableVisible = false; // 关闭弹框

        let name = that.creProj.projname;
        // let cityAdCode = that.creProj.cityObj.adcode;
        let keyWords = that.creProj.spidertype;
        let cityGeoJson = that.creProj.cityGeoJson;
        let resources = [that.creProj.scriptResource];
        let searchFUncStr = that.creProj.searchFunc;
        that.createProject(name, cityGeoJson, keyWords,resources,searchFUncStr);
      });
    },
    async delectProject(item) {
      let deletedId = item.id;
      console.log(deletedId);

      let _crawler = new Crawler.Crawler();
      await _crawler.init();
      let deleteResult = await _crawler.tryDeleteTask(deletedId);
      console.log(deleteResult);
      if(deleteResult){
        this.$message.success("删除成功");
      }else{
        this.$message.success("删除失败");
      }
      // this.$axios.delete("/poitask/PoiTask/id?id=" + deletedId).then(res => {
      //   this.initExistProjDatas();
      // });
    },
     submitUpload() {
        this.$refs.upload.submit();
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      beforeupload(file){
        
        file.text().then(x => {
          this.creProj.cityGeoJson = JSON.parse(x);
        });
        return false;
      },
    async exportCSV(item) {
      let exportId = item.id;
      let name = `${item.name}-${item.keyWords}.csv`;
      let _crawler = new Crawler.Crawler();
      await _crawler.init();
      let csvContent = "data:text/csv;charset=utf-8,\ufeff";
      if (window.navigator.msSaveOrOpenBlob) {
        csvContent = "\ufeff";
      }
      let csvLines = await _crawler.export2csv(exportId);
      csvContent = csvContent + csvLines ;
      if(window.navigator.msSaveOrOpenBlob){
        var blob = new Blob([decodeURIComponent(encodeURI(csvContent))],{
                  type: "text/csv;charset=utf-8;"
            });
        navigator.msSaveBlob(blob, name);
      }else{
        let encodedUri = encodeURI(csvContent);
        let link = document.createElement("a");
        link.setAttribute("href", encodedUri);
        link.setAttribute("download", name);
        document.body.appendChild(link);
        link.click();
      }
      // this.$axios
      //   .post(`/Poi/download/${exportId}`,{responseType:"arraybuffer"})
      //   .then(res => {
      //     let blob = new Blob([res.data],{type:"text/csv"});
      //     let url = window.URL.createObjectURL(blob);
      //     let link = document.createElement("a");
      //     link.style.display = "none";
      //     link.href = url;
      //     link.setAttribute("download",item.name+".csv");
      //     document.body.appendChild(link);
      //     link.click();
      //   });
    },
    viewDatas(item){
      console.log(item);
      let bound_geojson = item.geoJson;
      let center_obj = bound_geojson.features[0].properties.center;
      let center = [center_obj.lng, center_obj.lat];
      let routeUrl = this.$router.resolve({
        name:'viewdatas',
        query:{
          id:item.id,
          center:center
        }
      });
      window.open(routeUrl.href,'_blank');
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.main {
  width: 1140px;
  margin: 0 auto;
  padding-top: 30px;
}
.el-page-header {
  margin-bottom: 15px;
}
.itemsName {
  float: left;
  width: 150px;
  height: 40px;
}
.itemsName img {
  width: 40px;
  height: 40px;
  float: left;
}
.itemsName span {
  margin: 0;
  padding: 0;
  line-height: 40px;
  display: table-cell;
}
.addItems {
  margin: 0 0 0 auto;
  float: right;
}

.el-card {
  margin-top: 10px;
}
.creatspan {
  color: #409eff;
  text-decoration: underline;
  cursor: pointer;
}
.creatspan:hover{
  color:#2e5093
}
.el-form-item .big {
  width: 560px;
}
.el-form-item .small {
  width: 280px;
}
.el-cascader {
  margin: 0 auto 0 0;
}
#projLists li {
  list-style: none;
}
.el-card__header {
  background: #b8b1d1;
  border: #35266a;
  color: #43266a
}
.boxName span {
  float: left;
  font-size: 18px;
  line-height: 30px;
}
.boxContent {
  margin: -18px -10px;
}
.boxContent .leftcon {
  float: left;
  width: 80%;
}
.boxContent .rightcon {
  float: right;
  width: 20%;
  margin-top: 50px;
}
.leftcon div {
  width: 100%;
  height: 30px;
  margin: 5px 10px;
}
.rightcon div{
  width: 100%;
  height:30px;
  font-size: 12px;
  color:#8076a3;
  font-style: italic;
  font-weight: bold;
}
.boxContent .hn {
  float: left;
}
.boxContent .cn {
  float: left;
  display: block;
  margin-left: 10px;
}
.cityLevel {
  float: left;
  width: 50px;
  font-size: 12px;
  background: #e9ecef;
  text-align: center
}
.el-select {
  float: left;
}
.el-button--primary{
  background: #f6e7cc;
  color: #9a7835;
  border:#f6eecc
}
.el-button--primary:hover{
  background: #edd4a3;
}
</style>
