import Vue from 'vue'
import Router from 'vue-router'

import Catalog from '../components/Catalog.vue'
import Xingqd from '../components/Xingqudian.vue' 
import Viewdatas from '../components/Viewdatas.vue'
import Subway from '../components/Subway.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'catalog',
      component: Catalog
    },
    {
      path: '/xingqd',
      name: 'xingqd',
      component: Xingqd
    },
    {
      path:'/viewdatas/',
      name:'viewdatas',
      component:Viewdatas
    },
    {
      path:'/subway',
      name:'subway',
      component:Subway
    }
  ],
  mode: 'hash'
})