/*
*如何使用
import SubwayCrawler from "../fetchers/subway";
let temp = new SubwayCrawler.SubwayCrawler();
temp.getCityList(citylist => console.log(citylist));
temp.getGeoJson("1100",geojsons => console.log(geojsons));
*/

import coordtransform from 'coordtransform';

class SubwayCrawler {
    citylist = null;
    subway = null;
    constructor() {
        this.beijing_sub_way = null;
        this.subway = null;
        this.citylist = null;
        let that = this;
        window.cbk = function() {
            that.subway = window.subway;
            that.initCityList();
        };
        let scripts = [
            "https://webapi.amap.com/subway?v=1.0&key=0971acd2037ad6fda6f8020298651877&callback=cbk",
            "https://webapi.amap.com/maps?v=1.4.15&key=0971acd2037ad6fda6f8020298651877&plugin=AMap.LineSearch"
        ];
        this.loadScripts(scripts, () => {
            console.log("done");
        });
    }
    initCityList() {
        let that = this;
        let div = document.createElement("div");
        div.style.visibility = "hidden";
        div.id = "hidden_beijing_subway";
        document.body.appendChild(div);
        let beijing_subway = this.subway("hidden_beijing_subway", {
            adcode: 1100,
            theme: "colorful",
            client: 0,
            doubleclick: {
                switch: true
            }
        });
        beijing_subway.event.on("subway.complete", function(ev, info) {
            beijing_subway.getCityList(citylist => {
                that.citylist = citylist;
            });
        });
    }
    getCityList(callback) {
            if (!this.citylist) {
                setTimeout(() => {
                    this.getCityList(callback);
                }, 10);
            } else {
                callback(this.citylist);
            }
        }
        //city_ad_code是行政编码,可以通过getcitylist方法获得
    getGeoJson(city_ad_code, callback) {

        if (this.subway) {
            this.getCityLines(city_ad_code, x => {
                let points_geojson = {
                    "type": "FeatureCollection",
                    "features": [],
                };
                let line_geojson = {
                    "type": "FeatureCollection",
                    "features": [],
                };
                let count = 0;

                for (let i = 0; i < x.length; i++) {
                    let line = x[i];
                    // console.log(line);
                    let shortname = line.shortname;
                    let id = line.id;
                    let linesearch = new AMap.LineSearch({
                        pageIndex: 1,
                        city: city_ad_code,
                        pageSize: 5,
                        extensions: 'all'
                    });
                    linesearch.search(shortname, (status, result) => {
                        // console.log(shortname);
                        // console.log(JSON.stringify(result));
                        if (status === 'complete' && result.info === 'OK') {
                            //type
                            for (let i = 0; i < result.lineInfo.length; i++) {
                                let item = result.lineInfo[i];
                                let type = item.type;
                                if (type !== "地铁") { continue; }
                                //不是地铁的，直接干掉
                                //id去重
                                //city去掉不同城市的
                                //首尾反过来的保留一条
                                //如果一条线也没有，那就解析，插入

                                if (line_geojson.features.length > 0) {
                                    let id = item.id;
                                    let isExists = false;
                                    let isHasReverse = false;
                                    let start = item.start_stop;
                                    let end = item.end_stop;
                                    for (let j = 0; j < line_geojson.features.length; j++) {
                                        let existId = line_geojson.features[j].properties.id;
                                        if (existId === id) {
                                            isExists = true;
                                        }
                                        let _start = line_geojson.features[j].properties.start_stop;
                                        let _end = line_geojson.features[j].properties.end_stop;

                                        if (_start === end || _end === start) {
                                            isHasReverse = true;
                                        }
                                    }
                                    if (isExists || isHasReverse) continue; //id去重
                                    let points_feature = this.parse_to_point_feature(result.lineInfo[i]);
                                    let line_feature = this.parse_to_line_feature(result.lineInfo[i]);
                                    points_geojson.features = points_geojson.features.concat(points_feature);
                                    line_geojson.features.push(line_feature);
                                } else {
                                    //解析，插入
                                    let points_feature = this.parse_to_point_feature(result.lineInfo[i]);
                                    let line_feature = this.parse_to_line_feature(result.lineInfo[i]);
                                    points_geojson.features = points_geojson.features.concat(points_feature);
                                    line_geojson.features.push(line_feature);
                                }
                            }
                            count++;

                            if (count === x.length) {
                                callback({
                                    cityCode: city_ad_code,
                                    point_geo_json: points_geojson,
                                    line_geojson: line_geojson
                                });
                            }
                        } else {
                            // console.log("===========");
                            // console.log(result);
                        }
                    });
                }
            });


        } else {
            setTimeout(() => {
                this.getGeoJson(city_ad_code, callback);
            }, 10);
        }




    }

    parse_to_point_feature(line_search_result) {
        let features = [];
        for (let i = 0; i < line_search_result.via_stops.length; i++) {
            let stop = line_search_result.via_stops[i];
            let feature = {
                "type": "Feature",
                "properties": {
                    "id": stop.id,
                    "line_id": line_search_result.id,
                    "line_name": line_search_result.name,
                    "citycode": line_search_result.citycode,
                    "name": stop.name,
                    "sequence": stop.sequence
                },
                "geometry": {
                    "type": "Point",
                    "coordinates": coordtransform.gcj02towgs84(
                        stop.location.lng,
                        stop.location.lat //todo 坐标转换到WGS84
                    ),
                }
            };
            features.push(feature);
        }
        return features;
    }
    parse_to_line_feature(line_search_result) {
        let coordinates = [];
        for (let i = 0; i < line_search_result.path.length; i++) {
            let point = line_search_result.path[i];
            let lng = point.lng;
            let lat = point.lat;
            let coordinate = coordtransform.gcj02towgs84(lng, lat);
            coordinates.push(coordinate);
        }
        let feature = {
            "type": "Feature",
            "properties": {
                "id": line_search_result.id,
                "type": line_search_result.type,
                "name": line_search_result.name,
                "citycode": line_search_result.citycode,
                "start_stop": line_search_result.start_stop,
                "end_stop": line_search_result.end_stop,
                "stime": line_search_result.stime,
                "etime": line_search_result.etime,
                "uicolor": line_search_result.uicolor,
                "timedesc": line_search_result.timedesc,
                "loop": line_search_result.loop,
                "status": line_search_result.status,
                "direc": line_search_result.direc,
                "company": line_search_result.company,
                "distance": line_search_result.distance,
                "basic_price": line_search_result.basic_price,
                "total_price": line_search_result.total_price
            },
            "geometry": {
                "type": "LineString",
                "coordinates": coordinates
            }
        };

        return feature;
    }
    getCityLines(city_ad_code, callback) {
        let id = "city" + city_ad_code;
        let div = document.getElementById(id);
        if (div) {
            div.parentNode.removeChild(div);
        }
        div = document.createElement("div");
        div.style.visibility = "hidden";
        div.id = id;
        document.body.appendChild(div);
        let mySubway = this.subway(id, {
            adcode: city_ad_code,
            theme: "colorful",
            client: 0,
            doubleclick: {
                switch: true
            }
        });
        mySubway.event.on("subway.complete", function(ev, info) {
            mySubway.getLineList(x => callback(x));
        });
    }
    loadScripts(array, callback) {
        var loader = function(src, handler) {
            var script = document.createElement("script");
            script.src = src;
            script.onload = script.onreadystatechange = function() {
                script.onreadystatechange = script.onload = null;
                handler();
            }
            var head = document.getElementsByTagName("head")[0];
            (head || document.body).appendChild(script);
        };
        (function run() {
            if (array.length != 0) {
                loader(array.shift(), run);
            } else {
                callback && callback();
            }
        })();
    }

}
export default { SubwayCrawler };