<template>
  <div class="all">
    <div id="mymap" ref="rootmap"></div>
    <!-- <el-select class="styleSelect" v-model="mapStyle" size="mini" @change="changeMapstyle">
      <el-option v-for="item in mapStyles" :key="item.value" :label="item.label" :value="item.value"></el-option>
    </el-select>-->
    <el-radio-group class="styleSelect" v-model="mapStyle" @change="changeMapstyle" size="mini">
      <el-radio-button
        v-for="item in mapStyles"
        :key="item.value"
        :label="item.value"
      >{{item.label}}</el-radio-button>
    </el-radio-group>
    <div class="colorpickerGroup">
      <el-col :span="12">
        <span>边界颜色：</span>
        <el-color-picker
          v-model="boderColor"
          show-alpha
          size="small"
          @active-change="changeBordercolor"
        ></el-color-picker>
      </el-col>
      <el-col :span="12">
        <span>POI颜色：</span>
        <el-color-picker
          v-model="pointColor"
          show-alpha
          size="small"
          @active-change="changePointcolor"
        ></el-color-picker>
      </el-col>
      <el-col :span="12">
        <span>边界半径：{{this.borderRadius}}</span>
        <el-slider
          v-model="borderRadius"
          max="20"
          style="width:120px;"
          @change="changeBorderRadius"
        ></el-slider>
      </el-col>

      <el-col :span="12">
        <span>POI半径：{{this.pointRadius}}</span>
        <el-slider v-model="pointRadius" max="20" style="width:120px;" @change="changePointRadius"></el-slider>
      </el-col>
    </div>
  </div>
</template>
<script>
import Crawler from '../fetchers/crawler.js'
import "ol/ol.css";
import { Map, View } from "ol";
import GeoJSON from "ol/format/GeoJSON";
import { Tile as TileLayer, Vector as VectorLayer } from "ol/layer";
import { XYZ, Vector as VectorSource } from "ol/source";
import { Circle, Fill, Stroke, Style } from "ol/style";
export default {
  data() {
    return {
      mapboxKey:
        "pk.eyJ1Ijoiemhhbmdhc3luY2p1biIsImEiOiJjam9tYncyaXkwbWd4M29tdGRsMXczOGUzIn0.2Q1IDIWtQTSOTUn_S3iQ1A",
      dataId: this.$route.query.id,
      boundRes: undefined,
      bound_geojson: undefined,
      bound_center: undefined,
      poi_geojson: undefined,
      map: undefined,
      mapStyle: "light-v9",
      mapStyles: [
        {
          label: "light",
          value: "light-v9"
        },
        {
          label: "dark",
          value: "dark-v10"
        },
        {
          label: "streets",
          value: "streets-v11"
        },
        {
          label: "outdoors",
          value: "outdoors-v11"
        },
        {
          label: "satellite",
          value: "satellite-v9"
        }
      ],
      basicLayer: undefined,
      boderColor: "#f6e7cc",
      borderRadius: 2,
      pointColor: "#8076a3",
      pointRadius: 1,
      bound_Layer: undefined,
      poi_Layer: undefined
    };
  },
  mounted() {
    this.initMap();
  },
  methods: {
    async initMap() {
      this.basicLayer = new TileLayer({
        source: new XYZ({
          url:
            "https://api.mapbox.com/styles/v1/mapbox/" +
            this.mapStyle +
            "/tiles/256/{z}/{x}/{y}?access_token=" +
            this.mapboxKey
        }),
        zindex: 0
      });
      this.map = new Map({
        target: this.$refs.rootmap,
        layers: [this.basicLayer],
        view: new View({
          projection: "EPSG:4326",
          center: this.$route.query.center,
          zoom: 10
        })
      });

      let image = new Circle({
        radius: 1,
        fill: new Fill({
          color: "#8076a3"
        }),
        stroke: new Stroke({
          color: "#8076a3",
          width: 1
        })
      });
      let styles = {
        Point: new Style({
          image: image
        }),
        Polygon: new Style({
          stroke: new Stroke({
            color: "#f6e7cc",
            width: 2
          }),
          fill: null
        })
      };
      let _crawler = new Crawler.Crawler();
      await _crawler.init();
      let poiGeoJson = await _crawler.getGeoJson(this.dataId);
      let boundaryJson = await _crawler.getPoiTaskInfo(this.dataId);
      boundaryJson = boundaryJson.data.geoJson;
      this.bound_Layer = new VectorLayer({
              source: new VectorSource({
                features: new GeoJSON().readFeatures(boundaryJson)
              }),
              style: styles.Polygon,
              zIndex: 1
            });


      this.poi_Layer = new VectorLayer({
              source: new VectorSource({
                features: new GeoJSON().readFeatures(poiGeoJson)
              }),
              style: styles.Point,
              zIndex: 2
            });

      this.map.addLayer(this.bound_Layer);
      this.map.addLayer(this.poi_Layer);
      // this.$axios
      //   .all([
      //     this.$axios.get("/poitask/PoiTask/" + this.dataId),
      //     this.$axios.get("/poitask/PoiTask/geojson/" + this.dataId)
      //   ])
      //   .then(
      //     this.$axios.spread((boundRes, poiRes) => {
      //       let bound_geojson = JSON.parse(boundRes.data.data.geoJson);
      //       let bound_center_obj = bound_geojson.features[0].properties.center;
      //       let bound_center_arr = [bound_center_obj.lng, bound_center_obj.lat];
      //       let poi_arrs = JSON.parse(poiRes.data.data);
      //       let poi_geojson = {
      //         type: "FeatureCollection",
      //         features: poi_arrs
      //       };
      //       this.bound_Layer = new VectorLayer({
      //         source: new VectorSource({
      //           features: new GeoJSON().readFeatures(bound_geojson)
      //         }),
      //         style: styles.Polygon,
      //         zIndex: 1
      //       });
      //       this.poi_Layer = new VectorLayer({
      //         source: new VectorSource({
      //           features: new GeoJSON().readFeatures(poi_geojson)
      //         }),
      //         style: styles.Point,
      //         zIndex: 2
      //       });
      //       this.map.addLayer(this.bound_Layer);
      //       this.map.addLayer(this.poi_Layer);
      //     })
      //   );
    },
    changeMapstyle(mapstyle) {
      console.log(mapstyle);
      let newBasiclayer = new TileLayer({
        source: new XYZ({
          url:
            "https://api.mapbox.com/styles/v1/mapbox/" +
            mapstyle +
            "/tiles/256/{z}/{x}/{y}?access_token=" +
            this.mapboxKey
        }),
        zIndex: 0
      });
      this.map.removeLayer(this.basicLayer);
      this.map.addLayer(newBasiclayer);
      this.basicLayer = newBasiclayer;
    },
    changeBordercolor(color) {
      let borderStyle = new Style({
        stroke: new Stroke({
          color: color,
          width: this.borderRadius
        }),
        fill: null
      });
      this.bound_Layer.setStyle(borderStyle);
    },
    changePointcolor(color) {
      let pointImage = new Circle({
        radius: this.pointRadius,
        fill: new Fill({
          color: color
        }),
        stroke: new Stroke({
          color: color,
          width: 1
        })
      });
      let pointStyle = new Style({
        image: pointImage
      });
      this.poi_Layer.setStyle(pointStyle);
    },
    chnageBorderRadius(radius) {
      let borderStyle = new Style({
        stroke: new Stroke({
          color: this.boderColor,
          width: radius
        }),
        fill: null
      });
      this.bound_Layer.setStyle(borderStyle);
    },
    changePointRadius(radius) {
      let pointImage = new Circle({
        radius: radius,
        fill: new Fill({
          color: this.pointColor
        }),
        stroke: new Stroke({
          color: this.pointColor,
          width: 1
        })
      });
      let pointStyle = new Style({
        image: pointImage
      });
      this.poi_Layer.setStyle(pointStyle);
    }
  }
};
</script>
<style scoped>
.all {
  position: relative;
}
#mymap {
  position: absolute;
  height: calc(100vh);
  width: 100%;
}
.styleSelect {
  float: right;
  margin: 5px 5px 0 0;
}
.colorpickerGroup {
  position: absolute;
  width: 300px;
  height: 110px;
  background: white;
  left: 10px;
  top: calc(100vh - 120px);
}
.colorpickerGroup .el-col {
  padding: 5px;
}
</style>