<template>
    <div class="all">
        <div id="myMap" ref="rootmap"></div>
        <el-radio-group class="styleSelect" v-model="mapStyle" @change="changeMapstyle" size="mini">
        <el-radio-button
            v-for="item in mapStyles"
            :key="item.value"
            :label="item.value"
        >{{item.label}}</el-radio-button>
        </el-radio-group>
        <div class="layerMenu">
            <div class="menu-top">
                <div class="layerInfo">
                    <div class="layerInfo-text">当前城市：{{cityName}}</div>
                </div>
                <div class="pull-icon" v-bind:class="{'pull-up':isActive}" @click="changeIcon"></div>
            </div>
            <ul class="menu-content" v-if="isActive">
                
                   <li class="city-item" v-for="(item,key) in citylist" :key="item.city" :label="item.name">
                        <div class="city-item-content">
                            <div class="city-item-name">
                                {{item.name}}
                            </div>
                            <div class="city-item-dl" @click="downloadJSON(key)">
                                下载
                            </div>
                            <div class="city-item-see" @click="seeData(key)">
                                查看
                            </div>
                            
                        </div>
                   </li> 
            </ul>
        </div>
    </div>
</template>
<script>
import "ol/ol.css";
import { Map, View } from "ol";
import GeoJSON from "ol/format/GeoJSON";
import { Tile as TileLayer, Vector as VectorLayer } from "ol/layer";
import { XYZ, Vector as VectorSource } from "ol/source";
import { Circle, Fill, Stroke, Style } from "ol/style";
import centroid from "@turf/centroid";

import SubwayCrawler from "../fetchers/subway";

export default {
    data(){
        return {
            mapboxKey:"pk.eyJ1Ijoiemhhbmdhc3luY2p1biIsImEiOiJjam9tYncyaXkwbWd4M29tdGRsMXczOGUzIn0.2Q1IDIWtQTSOTUn_S3iQ1A",
            citylist:null,
            map: undefined,
            mapStyle: "light-v9",
            mapStyles: [
                {
                label: "light",
                value: "light-v9"
                },
                {
                label: "dark",
                value: "dark-v10"
                },
                {
                label: "streets",
                value: "streets-v11"
                },
                {
                label: "outdoors",
                value: "outdoors-v11"
                },
                {
                label: "satellite",
                value: "satellite-v9"
                }
            ],
            basicLayer: undefined,
            isActive:false,
            cityName:"北京",
        }
    },
    created(){
        this.getCityData();
    },
    mounted(){
        this.initMap();
    },
    methods:{
        getCityData(){
            let temp = new SubwayCrawler.SubwayCrawler();
            temp.getCityList(citylist => this.citylist = citylist);
        },
        initMap(){
            this.basicLayer = new TileLayer({
                source: new XYZ({
                url:
                    "https://api.mapbox.com/styles/v1/mapbox/" +
                    this.mapStyle +
                    "/tiles/256/{z}/{x}/{y}?access_token=" +
                    this.mapboxKey
                }),
                zindex: 0
            });
            this.map = new Map({
                target: this.$refs.rootmap,
                layers: [this.basicLayer],
                view: new View({
                    projection: "EPSG:4326",
                    center: [116.403694,39.91405],
                    zoom: 10
                })
            });
            

        },
        changeMapstyle(mapstyle) {
            let newBasiclayer = new TileLayer({
                source: new XYZ({
                url:
                    "https://api.mapbox.com/styles/v1/mapbox/" +
                    mapstyle +
                    "/tiles/256/{z}/{x}/{y}?access_token=" +
                    this.mapboxKey
                }),
                zIndex: 0
            });
            this.map.removeLayer(this.basicLayer);
            this.map.addLayer(newBasiclayer);
            this.basicLayer = newBasiclayer;
        },
        changeIcon(){
            this.isActive = !this.isActive
        },
        seeData(value){
            
            let temp = new SubwayCrawler.SubwayCrawler();
            let that = this;
            temp.getGeoJson(
                value,function(geojsons){
                    let line_geojson = geojsons.line_geojson;
                    let point_geo_json = geojsons.point_geo_json;

                    let point =centroid(geojsons.point_geo_json);
                    let coord = point.geometry.coordinates;

                    let image = new Circle({
                            radius: 2.5,
                            fill: new Fill({
                            color: "#8076a3"
                        })
                    });
                    let pointStyle = new Style({
                             image: image
                    });
                    let line_Layer = new VectorLayer({
                        source:new VectorSource({
                            features:new GeoJSON().readFeatures(line_geojson)
                        }),
                        //style:styles.Line,
                        style:function(feature,res){
                            let styles=[];
                            let color = `#${feature.get("uicolor")}`;
                            let lineStyle = new Style({
                                                stroke: new Stroke({
                                                    color: color,
                                                    width: 4
                                            })    
                             });
                             styles.push(lineStyle);
                             return styles    
                        },
                        zIndex:1
                    })
                    let point_Layer = new VectorLayer({
                        source:new VectorSource({
                            features:new GeoJSON().readFeatures(point_geo_json)
                        }),
                        style:pointStyle,
                        zIndex:2
                    })
                    that.map.getView().animate({
                        //center: this.citycenter[value]
                        center:coord
                    });
                    that.map.addLayer(line_Layer);
                    that.map.addLayer(point_Layer);
                }
            )
        },
        downloadJSON(value){
            let temp = new SubwayCrawler.SubwayCrawler();
            let that = this;
            temp.getGeoJson(
                value,function(geojsons){
                    var Link = document.createElement('a');
                    Link.download = `${value}.json`;
                    Link.style.display = 'none';
                    // 字符内容转变成blob地址
                    var blob = new Blob([JSON.stringify(geojsons)]);
                    Link.href = URL.createObjectURL(blob);
                    // 触发点击
                    document.body.appendChild(Link);
                    Link.click();
                    // 然后移除
                    document.body.removeChild(Link);

            })
        }
    }
}
</script>
<style scoped>
.all{
    margin:0;
    padding:0;
}
#myMap{
    position: fixed;
    height:100%;
    width:100%;
}
.styleSelect {
  /* float: right;
  margin: 5px 5px 0 0; */
  position: absolute;
  right:10px;
  top:10px;
}
.layerMenu{
  position: absolute;
  left:60px;
  top:10px;
  height:500px;
  width:300px;
}
.menu-top{
    height:40px;
    width:100%;
    background-color: rgba(5,12,41,0.8);
    
}
.menu-content{
    height: 450px;
    width:290px;
    padding:10px 0 10px 10px;
    background-color: rgba(5,12,41,0.8);
    margin-top:5px;
    overflow-y:auto;
}
.layerInfo{
    float:left;
    width:2x60px;
    height:100%;
}
.layerInfo-text{
    line-height: 40px;
    margin-left:20px;
    color: white;
}
.pull-icon{
    float:right;
    width:40px;
    height:100%;
    background-image: url("../assets/images/pull-down.png");
    background-size: 30%;
    background-repeat: no-repeat;
    background-position: center;
}
.pull-up{
    background-image: url("../assets/images/pull-up.png");
}
.city-item{
    margin:5px;
    height:40px;
    background: rgba(5,12,41,0.3);
    color:white
}
.city-item-content{
    line-height: 40px;
    padding:0 5px;
}


.city-item-name{
    float: left;
}
.city-item-see,.city-item-dl{
    float: right;
    width: 40px;
    font-size: 12px;
}

</style>