<template>

  <div class="vue-progress-bar__outer">
   <div class="vue-progress-bar__inner" :style="barStyle"></div>
  </div>

</template>
 
<script>
 export default {
  props:{
   height:{
    type: Number,
    default: 0,
    required: true,
    validator: val => val >= 0
   },
  //  color: {
  //   type: String,
  //   default: ''
  //  },
   percentage:{
    type: Number,
    default: 0,
    required: true,
    validator: val => val >= 0 && val <= 100
   }
  },
  computed:{
   barStyle() {
    const style = {};
    style.width = this.percentage + '%';
    style.height = this.height + 'px';
    //style.backgroundColor = this.color;
    return style;
   }
  }
 }
</script>
 
<style scoped>
@keyframes inner-bg-gradient
{
  0%   {background-image: linear-gradient(90deg, #b8b1d1 0%, #b8b1d1 100%);}
