  30%  {background-image: linear-gradient(90deg, #b8b1d1 0%, #8d709e 30%,#b8b1d1 100%);}
  40%  {background-image: linear-gradient(90deg, #b8b1d1 0%, #8d709e 40%,#b8b1d1 100%);}    
  50%  {background-image: linear-gradient(90deg, #b8b1d1 0%, #8d709e 50%,#b8b1d1 100%);}
  60%  {background-image: linear-gradient(90deg, #b8b1d1 0%, #8d709e 60%,#b8b1d1 100%);}
  70%  {background-image: linear-gradient(90deg, #b8b1d1 0%, #8d709e 70%,#b8b1d1 100%);}
  80%  {background-image: linear-gradient(90deg, #b8b1d1 0%, #8d709e 80%,#b8b1d1 100%);}
  90%  {background-image: linear-gradient(90deg, #b8b1d1 0%, #8d709e 90%,#b8b1d1 100%);}
  100% {background-image: linear-gradient(90deg, #b8b1d1 0%, #8d709e 100%);}
}
