  0%   {background-image: linear-gradient(90deg,#f3f3f3 0%, #f3f3f3 100%);}
  10%  {background-image: linear-gradient(90deg,#f3f3f3 0%, #ebebeb 10%,#f3f3f3 100%);}
  20%  {background-image: linear-gradient(90deg,#f3f3f3 0%, #ebebeb 20%,#f3f3f3 100%);}
