  40%  {background-image: linear-gradient(90deg,#f3f3f3 0%, #ebebeb 40%,#f3f3f3 100%);}    
  50%  {background-image: linear-gradient(90deg,#f3f3f3 0%, #ebebeb 50%,#f3f3f3 100%);}
